@keyframes fadeIn {
  0% {
    opacity: 0;
    visibility: hidden;
  }
  100% {
    opacity: 1;
    visibility: visible;
  }
}
@keyframes fadeInUp {
  0% {
    opacity: 0;
    visibility: hidden;
    transform: translateY(25px);
  }
  100% {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
  }
}
@keyframes slideLeft {
  0% {
    opacity: 0;
    visibility: hidden;
    transform: translateX(10%);
  }
  100% {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
  }
}
.snazzymenu {
  border-top: solid 1px rgba(0, 0, 0, 0.1);
  border-bottom: solid 1px rgba(0, 0, 0, 0.1);
  position: absolute;
  width: 100%;
  z-index: 999;
}
.snazzymenu ul li a {
  display: inline-block;
  padding: 5px;
}
.snazzymenu .menu {
  visibility: hidden;
  position: absolute;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.snazzymenu .menu.open {
  visibility: visible;
}
.snazzymenu .menu > li > a {
  padding: 1.5rem 1rem;
  text-align: center;
  text-transform: uppercase;
}
.snazzymenu .column-title > a {
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 0.5rem;
}
.snazzymenu.fixed {
  position: fixed;
  top: 0;
}

.close-overlay {
  position: fixed;
  background: rgba(0, 0, 0, 0.1);
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  z-index: 4;
  transition: all 0.2s ease;
}
.close-overlay.active {
  opacity: 1;
  visibility: visible;
}

.menu-btn {
  color: #fff;
  cursor: pointer;
  position: relative;
  display: block;
  margin-left: 0.5rem;
  outline: none;
  width: 30px;
  height: 22px;
  border: none;
  background: transparent;
  border-top: 4px solid;
  border-bottom: 4px solid;
  font-size: 0;
  transition: all 0.1s ease;
}
.menu-btn:before, .menu-btn:after {
  content: "";
  display: block;
  width: 100%;
  height: 4px;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  left: 0;
  background: currentColor;
  transition: transform 0.1s ease;
}
.menu-btn.active {
  border-color: transparent;
}
.menu-btn.active:before {
  transform: rotate(45deg);
}
.menu-btn.active:after {
  transform: rotate(-45deg);
}

.snazzymenu .controls {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.25rem 1rem;
}
.snazzymenu .controls .logo {
  margin-right: 1rem;
}
.snazzymenu .controls .logo a {
  padding: 0;
}
.snazzymenu .controls .logo img {
  max-width: 100%;
  height: auto;
}
.snazzymenu .controls .cta {
  display: flex;
  align-items: center;
}
.snazzymenu .controls a {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 0 0.5rem;
}
.snazzymenu .controls a span {
  margin-left: 0.25rem;
}
.snazzymenu .controls svg {
  fill: currentColor;
}

.snazzymenu.dark {
  background: rgba(0, 0, 0, 0.95);
}
.snazzymenu.dark ul li a {
  color: #fff;
}
.snazzymenu.dark .menu-toggle {
  color: #fff;
}
.snazzymenu.dark.mobile .menu.caret li.has-sub .toggle::after {
  border-right: solid 1px #fff;
  border-bottom: solid 1px #fff;
}
.snazzymenu.dark.mobile .menu.plus li.has-sub .toggle::after {
  background: linear-gradient(#fff, #fff), linear-gradient(#fff, #fff);
  background-position: center;
  background-size: 50% 2px, 2px 50%;
  background-repeat: no-repeat;
}
.snazzymenu.dark.desktop .menu {
  background: rgba(0, 0, 0, 0.95);
}
.snazzymenu.dark.desktop .menu.open > li.active > a {
  color: rgba(255, 255, 255, 0.6);
}
.snazzymenu.dark.desktop .menu.open > li.active > a::after {
  border-color: rgba(255, 255, 255, 0.6);
}
.snazzymenu.dark.desktop .menu > li.has-sub > a::after {
  border-right: solid 1px #fff;
  border-bottom: solid 1px #fff;
}
.snazzymenu.dark.desktop .menu .mega-menu {
  background-color: rgba(0, 0, 0, 0.95);
}
.snazzymenu.dark .controls a {
  color: #fff;
}

.snazzymenu.light {
  background: rgba(255, 255, 255, 0.97);
}
.snazzymenu.light ul li a {
  color: #000;
}
.snazzymenu.light .menu-btn {
  color: #000;
}
.snazzymenu.light.mobile .menu.caret li.has-sub .toggle::after {
  border-right: solid 1px #000;
  border-bottom: solid 1px #000;
}
.snazzymenu.light.mobile .menu.plus li.has-sub .toggle::after {
  background: linear-gradient(#000, #000), linear-gradient(#000, #000);
  background-position: center;
  background-size: 50% 2px, 2px 50%;
  background-repeat: no-repeat;
}
.snazzymenu.light.desktop .menu {
  background: rgba(255, 255, 255, 0.97);
}
.snazzymenu.light.desktop .menu.open > li.active > a {
  color: rgba(0, 0, 0, 0.6);
}
.snazzymenu.light.desktop .menu.open > li.active > a::after {
  border-color: rgba(0, 0, 0, 0.6);
}
.snazzymenu.light.desktop .menu > li.has-sub > a::after {
  border-right: solid 1px #000;
  border-bottom: solid 1px #000;
}
.snazzymenu.light.desktop .menu .mega-menu {
  background: rgba(255, 255, 255, 0.97);
}
.snazzymenu.light .controls a {
  color: #000;
}

.snazzymenu.desktop.no-controls .controls {
  display: none;
}
.snazzymenu.desktop.no-controls .menu {
  visibility: visible;
  flex-direction: row;
  justify-content: space-around;
}
.snazzymenu.desktop.no-controls .menu > li > a {
  padding: 1rem;
}
.snazzymenu.desktop.no-controls .menu .menu-item-has-children.active .sub-menu {
  visibility: visible;
  height: auto !important;
}
.snazzymenu.desktop.no-controls .menu .menu-item-has-children.active .column-title {
  animation: fadeIn 0.1s ease;
  animation-delay: 0.05s;
  animation-fill-mode: both;
}

.snazzymenu.desktop .menu.open {
  flex-direction: row;
  justify-content: space-evenly;
  height: auto;
  animation: fadeIn 0.1s ease;
  animation-delay: 0.05s;
  animation-fill-mode: both;
}
.snazzymenu.desktop .menu.open > li.active .mega-menu {
  display: flex !important;
  flex-direction: row !important;
  visibility: visible;
  height: auto !important;
}
.snazzymenu.desktop .menu.open > li.active .mega-menu .sub-menu {
  display: block !important;
  height: auto !important;
}
.snazzymenu.desktop .menu.open > li.active .mega-menu .column-title {
  animation: fadeIn 0.1s ease;
  animation-delay: 0.05s;
  animation-fill-mode: both;
}
.snazzymenu.desktop .menu > li.has-sub > a::after {
  content: "";
  display: inline-block;
  width: 8px;
  height: 8px;
  position: relative;
  top: -4px;
  right: -0.5rem;
  transform: rotate(45deg);
}
.snazzymenu.desktop .menu > li.has-sub .toggle {
  display: none;
}
.snazzymenu.desktop .mega-menu {
  position: absolute;
  left: 0;
  width: 100%;
  padding: 1rem 1rem 2rem;
  display: flex;
  justify-content: center;
  visibility: hidden;
}
.snazzymenu.desktop .mega-menu > li {
  flex: 1;
  padding: 0 5px;
}
.snazzymenu.desktop .mega-menu > li > a {
  width: 100%;
}
.snazzymenu.desktop .mega-menu .column-title img {
  display: block;
  margin: 0 auto 1rem;
  width: 100%;
}
.snazzymenu.desktop .mega-menu .sub-menu {
  display: flex;
  flex-direction: column;
}
.snazzymenu.desktop .mega-menu .sub-menu li a:hover {
  text-decoration: underline;
}

.snazzymenu.mobile.scroll {
  height: 100vh;
}
.snazzymenu.mobile.scroll.fixed {
  overflow-y: scroll;
}
.snazzymenu.mobile .mega-menu {
  padding: 0;
}
.snazzymenu.mobile .mega-menu img {
  display: none !important;
}
.snazzymenu.mobile .sub-menu {
  display: flex;
  flex-direction: column;
  height: 0;
  overflow: hidden;
}
.snazzymenu.mobile .sub-menu li a {
  padding-left: 1.5rem;
}
.snazzymenu.mobile .sub-menu .sub-menu li a {
  padding-left: 3rem;
}
.snazzymenu.mobile .menu {
  padding-top: 2rem;
  padding-bottom: 4rem;
}
.snazzymenu.mobile .menu.caret li.has-sub .toggle::after {
  content: "";
  display: inline-block;
  width: 10px;
  height: 10px;
  right: 5px;
  position: relative;
  transform: rotate(45deg);
}
.snazzymenu.mobile .menu.caret li.open > .toggle::after {
  transform: rotate(-135deg) !important;
}
.snazzymenu.mobile .menu.plus li.has-sub .toggle {
  height: 50px;
}
.snazzymenu.mobile .menu.plus li.has-sub .toggle::after {
  content: "";
  display: inline-block;
  width: 24px;
  height: 24px;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  right: 15px;
}
.snazzymenu.mobile ul li.has-sub {
  position: relative;
}
.snazzymenu.mobile ul li.has-sub .toggle {
  position: absolute;
  width: 40px;
  top: 0;
  right: 0;
  margin-bottom: 0;
  width: 100%;
  text-align: right;
}
.snazzymenu.mobile ul li.open > a {
  text-decoration: underline;
  font-weight: 700;
}
.snazzymenu.mobile ul li.open > .toggle::after {
  background-size: 50% 2px !important;
}
.snazzymenu.mobile ul li.open li.open > a {
  text-decoration: none;
}
.snazzymenu.mobile ul li.column-title > a {
  font-weight: normal;
}
.snazzymenu.mobile ul li a {
  padding: 15px;
  text-align: left;
}
.snazzymenu.mobile .cta span {
  display: none;
}